@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;1,100;1,300&display=swap);
body {
  margin: 0;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  
}

.app{
  background-color: #141414;
  font-family: 'Roboto', 'Times New Roman', Times, serif;
  overflow-x: hidden;
}
.navbar{
    position: fixed;
    height: 10%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1;
}

.router{
    text-decoration: none;
    color: white;
}

.router:hover{
    color: rgba(60, 179, 113) !important;
}

.navbar_img{
    font-size: 2rem !important;
    cursor: pointer;
    color: rgba(60, 179, 113) !important;
    margin: 20px;
}

.navbar_elements{
    color: white;
    flex: 1 1;
}

.navbar_list{
    font-weight: 700;
    display: flex;
    justify-content: space-evenly;
    margin: 20px 0;
    list-style-type: none;
    
}

.navbar_list_item{
    position: relative;
    transition: all .3s;
    padding: 5px 20px;
    border-radius: 4px;
    cursor: pointer;
}

.navbar_list_item:after{
    content: "";
    height: 1px;
    left: 10%;
    bottom: -3px;
    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, center 0.3s ease 0s;
    width: 0;
}

.navbar_list_item:hover{
  color:  rgba(60, 179, 113);
}
.navbar_list_item:hover:after{
   width: 80%;
}

.nav_black{
    background-color: black;
    transition: all .2s;
}

.icono_menu{
    display: none !important;
}


@media (max-width: 680px) {
    .navbar_logo{
        display: none;
    }

    .navbar_elements{
        display: none;
    }

    .nav_black{
        background-color: transparent;
    }

    .sidebar{
        position: absolute;
        z-index: 1000;
        padding: 20px;
        color: white;
        right: 0;
        width: 30vw;
        font-weight: bolder;
        background-color: rgba(0, 0, 0, 0.9);
        height: 100vh;
        top: 0;
        visibility: unset;
    }
    
    .sidebar_list{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }

    .sidebar_list_element{
        list-style: none;
        margin: 30px 0;
        position: relative;
        cursor: pointer;
    }

    .sidebar_list_element:after{
        content: "";
        height: 1px;
        left: 0;
        bottom: -13px;
        position: absolute;
        background: #fff;
        width: 100%;
        transition: all .2s;
    }

    .sidebar_list_element:hover:after{
        background-color: rgba(60, 179, 113);
    }
    
    .icono_menu{
        display: unset !important;
        padding: 12px;
        color: white;
        cursor: pointer;
        font-size: 2rem !important;
        border-radius: 50%;
        margin-top: 20px;
        margin-left: 10px;
        background-color: rgba(60, 179, 113);
    }
}
.home_container{
    height: 100vh;
    width: 100vw;
    background: linear-gradient( rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9) ), url(/static/media/prueba_fondo.2957b218.jpeg) center center no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.home_banner{
    display: flex;
    height: 500px;
    width: 700px;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
}

.home_title{
    font-size: 5rem;
    font-weight: 400;
    color: white;
}

.home_title_span{
    color: mediumseagreen;
}

.home_description{
    font-size: 2rem;
    color: white;
}

.home_buttons{
    display: flex;
    align-items: center;
    transition: all .3s;
}

.home_button{
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 300;
    padding: 15px 20px;
    border-radius: 10px;
    border: 1px solid rgba(60, 179, 113);
    background: rgba(60, 179, 113, 0.9);
    color: white;
    margin: 10px;
    text-transform: uppercase;
    cursor: pointer;
    letter-spacing: 1px;
}

.home_button_text{
    transition: all .3s;
}

.home_buttons:hover .home_button_text{
   margin-right: .5rem;
}

.home_button:hover{
    background-color:rgba(60, 179, 113, 1);
}

@media (max-width: 680px) {
    .home_banner{
        width: auto;
    }
    .home_title{
        font-size: 3rem;
    }

    .home_description{
        font-size: 1.5rem;
    }
    .home_button{
        font-size: 1rem;
        font-weight: 300;
        padding: 10px 15px;
        margin: 5px;
    }
}
.aboutus_container{
    height: 60vh;
    width: 100vw;
    color: white;
    text-align: center;
    margin-top: 60px;
    position: relative;
    display: flex;
    justify-content: space-between;
}

.aboutus_img{
    width: 40vw;
    height: 50vh;
    left: 0;
}

.aboutus_right{
    display: flex;
    flex: 1 1;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 70px;
}

.aboutus_title{
    font-size: 3rem;
    font-weight: 800;
    letter-spacing: 2px;
    margin-bottom: 30px;
}

.aboutus_info{
    letter-spacing: 3px;
    text-align: left;
    padding-right: 30px;
    font-size: 20px;
    line-height: 40px;
    margin-bottom: 30px;
}

.aboutus_button{
    padding: 10px 30px;
    font-size: 20px;
    border: none;
    border-radius: 4px;
    letter-spacing: 3px;
    background-color:rgba(60, 179, 113, 0.8);
    color: white;
    cursor: pointer;
    transition: all .2s;
}

.aboutus_button:hover{
    background-color:rgba(60, 179, 113, 1);
}

@media (max-width: 680px) {
    .aboutus_img{
        display: none;
    }

    .aboutus_title{
        font-size: 2rem;
    }

    .aboutus_info{
        font-size: 1rem;
    }
}


.clients_container{
    padding: 50px;
    height: 70%;
    background: linear-gradient(180deg, #fbfdfe 60%, #141414 40%);
}

.clients_title{
    font-size: 3rem;
    font-weight: 800;
    letter-spacing: 2px;
    margin-bottom: 30px;
}

.clients_info{
    letter-spacing: 3px;
    text-align: left;
    padding-right: 30px;
    font-size: 20px;
    line-height: 40px;
    margin-bottom: 30px;
}
.clients_cards{
    display: flex;
    justify-content: space-between;
}

.client_card{
    background: white;
    border-radius: 10px;
    width: 30%;
    height: 70%;
    transition: all .2s;
}

.client_img{
    border-radius: 5px 5px 0 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    overflow: hidden;
}

.client_name{
    padding: 30px 30px 30px 10px;
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: center;
}

.client_card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: whitesmoke;
}

@media (max-width: 1440px) {
    .clients_container{
      height: 90%;
    }
}

@media (max-width: 680px) {
    .clients_cards{
        display: flex;
        flex-direction: column;
    }
    .client_card{
        background: white;
        border-radius: 10px;
        width: 100%;
        height: 50%;
        transition: all .2s;
        margin-bottom: 30px;
        box-shadow: 10px 10px 10px 1px;
    }
}
.services_container{
    display: flex;
    color: white;
    height: 80%;
    width: 100vw;
    justify-content: space-between;
}

.services_left{
    text-align: left;
    padding: 40px;
    width: 50vw;
}

.services_left_title{
    font-size: 3rem;
    letter-spacing: 3px;
    font-weight: bold;
    margin-bottom: 40px;
}

.services_img{
    width: 600px;
    height: 400px;
    object-fit: cover;
}

.services_right{
    justify-content: flex-start;
    width: 50vw;
    padding: 100px 30px 50px 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.services_element{
    display: flex;
    align-items: center;
    position: relative;
}

.services_element:before{
    top: -30px;
    content: "";
    position: absolute;
    width: 100%;
    /* #312e2d; */
    border-top: 4px solid rgba(60, 179, 113, 1);
    height: 2px;
}

.services_element_numeration{
    font-size: 4rem;
    font-weight: bolder;
    color: #666769;
    margin-right: 50px;
}

.services_element_title{
    font-weight: bold;
    font-size: 2rem;
    letter-spacing: 3px;
}

.services_element_description{
    font-weight: 500;
    font-size: 1rem;
    margin-top: 10px;
    letter-spacing: 2px;
}

@media (max-width: 1440px) {
    .services_container{
      height: 100%;
    }
}

@media (max-width: 680px) {
    .services_container{
        display: flex;
        flex-direction: column;
    }
    .services_img{
        width: 300px;
        height: 300px;
        object-fit: cover;
    }

    .services_right{
        justify-content: flex-start;
        width: auto;
    }

    .services_element{
        margin-bottom: 60px;
    }

    .services_element_numeration{
        font-size: 4rem;
        font-weight: bolder;
        color: #666769;
        margin-right: 25px;
    }
}
.products_container{
    display: flex;
    justify-content: space-between;
    padding: 40px;
    background-color: white;
    color: black;
    margin-top: 50px;
}

.rec-arrow-left, .rec-arrow-right{
    background: rgba(60, 179, 113, 0.5) !important;
}

.rec-arrow-left:hover, .rec-arrow-right:hover{
    background: rgba(60, 179, 113, 1) !important;
}

.rec-dot_active{
    background-color: rgba(60, 179, 113,.5) !important;
    box-shadow: 0 0 1px 3px rgba(60, 179, 113, 1) !important;
}

.products_carousel{
    width: 100%;
    padding-top: 110px;
}

.products_carousel img{
    width: 500px;
    height: 400px;
    object-fit: cover;
}

.products_left{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.products_left_title{
    font-size: 3rem;
    font-weight: bold;
    letter-spacing: 3px;
}

.products_element{
    display: flex;
    align-items: center;
    position: relative;
}

.products_element:before{
    top: -30px;
    content: "";
    position: absolute;
    width: 95%; 
    border-top: 4px solid rgba(60, 179, 113, 1);
    height: 2px;
}

.products_element_numeration{
    font-size: 4rem;
    font-weight: bolder;
    color: #666769;
    margin-right: 50px;
}

.products_element_title{
    font-weight: bold;
    font-size: 2rem;
    letter-spacing: 3px;
}

.products_element_description{
    font-weight: 500;
    font-size: 20px;
    margin-top: 10px;
    letter-spacing: 2px;
}
@media (max-width: 680px) {
    .products_container{
        flex-direction: column;
    }

    .products_left_title{
        font-size: 3rem;
        margin-bottom: 50px;
    }

    .products_element{
        margin-bottom: 60px;
    }

    .products_carousel{
        padding-top: 0;
    }

    .rec-arrow{
        display: none;
    }
}

.testimonials_container{
    height: 550px;
    width: 100vw;
    color:#fbfdfe;
    display: flex;
    padding-top: 50px;
    flex-direction: column;
    align-items: center;
}

.testimonial_title{
    font-size: 3rem;
    letter-spacing: 3px;
    font-weight: bold;
}

.testimonials_elements{
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    
}

.testimonial_element{
    width: 100%;
    background-color: #fbfdfe;
    color: black;
    border-radius: 10px;
    box-shadow: 1px 1px 10px;
    padding: 20px;
    margin: 0px 30px;
    display: flex;
    flex-direction: column;
    transition: all .2s;
}

.testimonial_element:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 1px 1px 13px;
}

.testimonial_element_img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    padding: 20px 0;
    /* rgba(60, 179, 113, 1); */
}

.testimonial_element_info{
    display: flex;
    flex-direction: column;
}

.testimonial_element_name{
    font-size: 1.5rem;
    margin-bottom: 10px;
    letter-spacing: 2px;
    font-weight: bold;
}

.testimonial_element_position{
    margin-bottom: 10px;
    font-size: 1.2rem;
    letter-spacing: 1px;
    font-weight: 700;
}

.testimonial_element_description{
    font-size: 1.1rem;
    letter-spacing: 2px;
}

.quote{
    color: rgba(60, 179, 113, 1);
    font-size: 2rem !important;
}

@media (max-width: 680px) {

    .testimonials_container{
        height: auto;
        width: 300px;
    }

    .testimonials_elements{
        flex-direction: column;
        justify-content: space-evenly;
        
    }

    .testimonial_element{
        width: 100%;
        padding: 10px;
        margin-bottom: 30px;
    }

    .testimonial_title{
        font-size: 2rem;
    }

    .testimonials_elements{
        margin-top: 30px;
    }

    .testimonial_element_img{
        width: 80px;
        height: 80px;
        border-radius: 50%;
        padding: 20px 0;
        /* rgba(60, 179, 113, 1); */
    }
    
    .testimonial_element_info{
        display: flex;
        flex-direction: column;
    }
    
    .testimonial_element_name{
        font-size: 1rem;
    }
    
    .testimonial_element_position{
        margin-bottom: 10px;
        font-size: .8rem;
    }
    
    .testimonial_element_description{
        font-size: 1rem;
    }
    
}
.contact_container {
  height: 500px;
  background-color: white;
  display: flex;
  justify-content: space-evenly;
  padding: 40px;
}
.contact_social_media {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.contact_element_container {
  display: flex;
  align-items: center;
}

.contact_element_description_container {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.contact_description {
  margin-bottom: 10px;
  font-weight: bold;
}

.icon {
  color: rgba(60, 179, 113, 1);
  font-size: 2.5rem !important;
}

.contact_form_container {
  width: 40vw;
}

.contact_form_title {
  font-size: 2rem;
  letter-spacing: 3px;
  color: rgba(60, 179, 113, 1);
}

.contact_form {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-size: 1.3rem;
  letter-spacing: 3px;
  padding: 20px;
  justify-content: space-evenly;
}

.contact_input_info {
  display: flex;
  flex-direction: column;
}

.form_input {
  text-decoration: none;
  font-family: 'Roboto';
  outline: none;
  font-size: 1rem;
  letter-spacing: 2px;
  margin-top: 10px;
  padding: 10px;
  max-width: 100%;
}

.btn_enviar {
  width: 100px;
  font-size: 1rem;
  font-weight: 500;
  padding: 10px 10px;
  text-align: center;
  border-radius: 10px;
  border: 1px solid rgba(60, 179, 113);
  background: rgba(60, 179, 113, 0.9);
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.btn_enviar:hover {
  background-color: rgba(60, 179, 113, 1);
}

@media (max-width: 680px) {
  .contact_container {
    flex-direction: column;
    height: auto;
  }

  .contact_element_container{
    margin-bottom: 30px;
  }

  .contact_social_media {
    width: 100%;
  }

  .contact_form_container {
    width: 100%;
  }

  .form_input{
    margin-bottom: 30px;
  }
}

.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1 1;
    color: white;
    margin: 0;
    padding: 15px;
    height: 30px;
}
.image_slider_container {
